/** @format */

.Header {
  position: sticky;
  top: 0;
  z-index: 111;
  padding: 21px 10px 20px 10px;
  display: grid;
  grid-template-columns: 10% 60% 30%;
  background-color: #ffffff;
  box-shadow: 0 2px 11px 0 rgba(0, 0, 0, 0.08);
}

a {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.Header div {
  margin: auto;
}

.Header_Text {
  margin-left: 3.4% !important;
  text-align: start;
}

.Header_Text .Title h1 {
  font-family: Roboto, "sans-serif";
  font-size: 18px;
  font-weight: bold;
  color: #000000;
}

.Header_Text .Sub p {
  opacity: 0.7;
  font-family: Roboto, "sans-serif";
  font-size: 12px;
  color: #000000;
}

.Next button {
  font-family: Roboto;
  padding: 9px 17px 9px 17px;
  border-radius: 4px;
  background-color: #12b0ee;
}

.Next button svg {
  align-self: center;
  margin-left: 10px;
  transform: rotateY(180deg);
}

.LeftArrow {
  align-self: center;
  margin: auto;
}

.Next button:disabled {
  background-color: #dfe2e7;
  box-shadow: none;
  opacity: 0.3;
}
